.container {
    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;
    top: 0;
    right: 0;

    /* text */
    color: var(--text-secondary, #5E7793);
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 148%;
    /* 20.72px */

    cursor: pointer;
}

.header {
    display: flex;
    background-color: transparent;
    border: none;
    text-align: right;
    opacity: 0.87;
}

.filtered {
    display: flex;
    justify-content: center;
    align-items: center;

    /* text */
    color: #002CFB;
    text-align: right;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    /* 114.286% */
    position: relative;
    top: 0;
    right: 0;
}

