/* Body row */
.gridrow {
    display: grid;
    grid-template-columns: 53px 86px 130px 326px 214px 200px 360px;
    height: 65px;
    align-items: center;
    border-top: 1px solid #E9EDF3;
    border-bottom: 1px solid #E9EDF3;

    /* text */
    color: var(--text, #122945);
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    /* 21px */
}

